.mission-mgmt-container {
  display: flex;
  flex-direction: column;
  max-height: 90vh;
  min-width: 600px;
  background: #fff;
  border-radius: 12px;
  overflow: hidden;

  .header {
    padding: 20px 24px;
    background: #f8f9fa;
    border-bottom: 1px solid #eee;
    position: relative;

    h2 {
      margin: 0;
      font-size: 1.25rem;
      font-weight: 600;
      color: #1a1a1a;
    }

    .course-name {
      margin: 4px 0 0;
      font-size: 0.9rem;
      color: #666;
    }

    .close-btn {
      position: absolute;
      top: 16px;
      right: 16px;
    }
  }

  .content {
    padding: 24px;
    overflow-y: auto;
    flex: 1;

    .loading-state, .empty-state {
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      padding: 60px 0;
      color: #666;

      mat-icon {
        font-size: 48px;
        width: 48px;
        height: 48px;
        margin-bottom: 16px;
        opacity: 0.5;
      }
    }

    .mission-list {
      .list-header {
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-bottom: 20px;

        h3 {
          margin: 0;
          font-size: 1.1rem;
          font-weight: 600;
        }
      }

      .week-badge {
        background: #e3f2fd;
        color: #1976d2;
        padding: 2px 8px;
        border-radius: 4px;
        font-size: 0.8rem;
        font-weight: 600;
        margin-right: 12px;
      }

      .status-tag {
        font-size: 0.75rem;
        padding: 2px 6px;
        border-radius: 4px;
        margin-right: 12px;
        text-transform: uppercase;

        &.draft { background: #f5f5f5; color: #757575; }
        &.published { background: #e8f5e9; color: #2e7d32; }
      }

      .mission-details {
        padding: 16px 0;

        .desc {
          color: #666;
          margin-bottom: 20px;
          line-height: 1.5;
        }

        .action-grid {
          display: grid;
          grid-template-columns: repeat(3, 1fr);
          gap: 16px;

          .action-card {
            display: flex;
            flex-direction: column;
            align-items: center;
            padding: 16px;
            border: 1px solid #eee;
            border-radius: 8px;
            cursor: pointer;
            transition: all 0.2s;

            &:hover:not(.disabled) {
              background: #f0f7ff;
              border-color: #1976d2;
              transform: translateY(-2px);
            }

            &.disabled {
              opacity: 0.5;
              cursor: not-allowed;
              background: #fafafa;
            }

            &.danger:hover {
              background: #fff5f5;
              border-color: #f44336;
            }

            mat-icon {
              margin-bottom: 8px;
            }

            span {
              font-weight: 500;
              font-size: 0.9rem;
              margin-bottom: 4px;
            }

            small {
              font-size: 0.75rem;
              color: #888;
            }
          }
        }
      }
    }
  }
}
